<template>
  <div>
    <table class="table">
      <thead>
        <tr>
          <th>{{ spent.name }}</th>
          <th>{{ spent.datetime }}</th>
          <th>{{ spent.amount }}</th>
        </tr>
      </thead>
    </table>
  </div>
</template>

<script>
  export default {
    data: function () {
      return {
        'spent': { name: 'Nom', datetime: 'jamais', amount: 'infini' }
      }
    }
  }
</script>



